.login_limiter{
	body, html {
		height: 100%;
		font-family: Poppins-Regular, sans-serif;
	}
	
	/*---------------------------------------------*/
	a {
		font-family: Poppins-Regular;
		font-size: 14px;
		line-height: 1.7;
		color: #666666;
		margin: 0px;
		transition: all 0.4s;
		-webkit-transition: all 0.4s;
	  -o-transition: all 0.4s;
	  -moz-transition: all 0.4s;
	}
	
	a:focus {
		outline: none !important;
	}
	
	a:hover {
		text-decoration: none;
	  color: rgb(211,71,54);
	}
	
	/*---------------------------------------------*/
	h1,h2,h3,h4,h5,h6 {
		margin: 0px;
	}
	
	p {
		font-family: Poppins-Regular;
		font-size: 14px;
		line-height: 1.7;
		color: #666666;
		margin: 0px;
	}
	
	ul, li {
		margin: 0px;
		list-style-type: none;
	}
	
	
	/*---------------------------------------------*/
	input {
		outline: none;
		border: none;
	}
	
	textarea {
	  outline: none;
	  border: none;
	}
	
	textarea:focus, input:focus {
	  border-color: transparent !important;
	}
	
	input:focus::-webkit-input-placeholder { color:transparent; }
	input:focus:-moz-placeholder { color:transparent; }
	input:focus::-moz-placeholder { color:transparent; }
	input:focus:-ms-input-placeholder { color:transparent; }
	
	textarea:focus::-webkit-input-placeholder { color:transparent; }
	textarea:focus:-moz-placeholder { color:transparent; }
	textarea:focus::-moz-placeholder { color:transparent; }
	textarea:focus:-ms-input-placeholder { color:transparent; }
	
	input::-webkit-input-placeholder { color: #acacac; }
	input:-moz-placeholder { color: #acacac; }
	input::-moz-placeholder { color: #acacac; }
	input:-ms-input-placeholder { color: #acacac; }
	
	textarea::-webkit-input-placeholder { color: #acacac; }
	textarea:-moz-placeholder { color: #acacac; }
	textarea::-moz-placeholder { color: #acacac; }
	textarea:-ms-input-placeholder { color: #acacac; }
	
	/*---------------------------------------------*/
	button {
		outline: none !important;
		border: none;
		background: transparent;
	}
	
	button:hover {
		cursor: pointer;
	}
	
	iframe {
		border: none !important;
	}
	.logo_login{
		text-align: center;
		
	}
	
	.logo_login img{
		width: 20%;
		margin: auto;
	}
	/*//////////////////////////////////////////////////////////////////
	[ Utility ]*/
	.txt1 {
	  font-family: Raleway-Regular;
	  font-size: 16px;
	  color: #999999;
	  line-height: 1.4;
	}
	
	.bo1 {
	  border-bottom: 1px solid #999999;
	}
	
	.hov1:hover {
	  border-color: rgb(211,71,54);
	}
	
	
	/*//////////////////////////////////////////////////////////////////
	[ login ]*/
	
	.limiter {
	  width: 100%;
	  margin: 0 auto;
	}
	
	.container-login100 {
	  width: 100%;  
	  min-height: 100vh;
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  flex-wrap: wrap;
	  justify-content: center;
	  align-items: center;
	  padding: 15px;
	  background: #d5dae1;
	  position: relative;
	  z-index: 1;
	}
	
	.container-login100::before {
	  content: "";
	  display: block;
	  position: absolute;
	  z-index: -1;
	  width: 100%;
	  height: 100%;
	  top: 0;
	  left: 0;
	  background-image: url('../../resources/images/img-01.png');
	  background-repeat: no-repeat;
	  background-size: cover;
	  background-position: center;
	}
	
	.wrap-login100 {
	  width: 450px;
	  background: #fff;
	  border-radius: 3px;
	  overflow: hidden;
	}
	
	
	
	/*------------------------------------------------------------------
	[  ]*/
	.login100-form {
	  width: 100%;
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  justify-content: space-between;
	  flex-wrap: wrap;
	}
	
	.login100-form-title {
	  font-family: Raleway-Black;
	  font-size: 23px;
	  color: #333333;
	  line-height: 1.2;
	  text-transform: uppercase;
	  text-align: center;
	
	  width: 100%;
	  display: block;
	}
	
	
	/*---------------------------------------------*/
	.wrap-input100 {
	  position: relative;
	  width: 100%;
	  z-index: 1;
	}
	
	.input100 {
	  font-family: Raleway-SemiBold;
	  font-size: 18px;
	  line-height: 1.2;
	  color: #686868;
	
	  display: block;
	  width: 100%;
	  background: #e6e6e6;
	  height: 45px;
	  border-radius: 3px;
	  padding: 0 5px 0 5px;
	}
	.input100::placeholder{
		font-weight: 100;
		font-family: arial;
		font-size:0.9em;
	}
	
	
	/*------------------------------------------------------------------
	[ Focus ]*/
	.focus-input100 {
	  display: block;
	  position: absolute;
	  border-radius: 3px;
	  bottom: 0;
	  left: 0;
	  z-index: -1;
	  width: 100%;
	  height: 100%;
	  box-shadow: 0px 0px 0px 0px;
	  color: rgba(211,63,141, 0.6);
	}
	
	.input100:focus + .focus-input100 {
	  -webkit-animation: anim-shadow 0.5s ease-in-out forwards;
	  animation: anim-shadow 0.5s ease-in-out forwards;
	}
	
	@-webkit-keyframes anim-shadow {
	  to {
		box-shadow: 0px 0px 60px 20px;
		opacity: 0;
	  }
	}
	
	@keyframes anim-shadow {
	  to {
		box-shadow: 0px 0px 60px 20px;
		opacity: 0;
	  }
	}
	
	.symbol-input100 {
	  font-size: 24px;
	  color: #999999;
	
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  align-items: center;
	  position: absolute;
	  border-radius: 25px;
	  bottom: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  padding-left: 23px;
	  padding-bottom: 5px;
	  pointer-events: none;
	
	  -webkit-transition: all 0.4s;
	  -o-transition: all 0.4s;
	  -moz-transition: all 0.4s;
	  transition: all 0.4s;
	}
	
	.input100:focus + .focus-input100 + .symbol-input100 {
	  color: rgb(211,71,54);
	  padding-left: 18px;
	}
	
	/*==================================================================
	[ Restyle Checkbox ]*/
	
	.input-checkbox100 {
	  display: none;
	}
	
	.label-checkbox100 {
	  font-family: Raleway-Regular;
	  font-size: 16px;
	  color: #999999;
	  line-height: 1.2;
	
	  display: block;
	  position: relative;
	  padding-left: 26px;
	  cursor: pointer;
	}
	
	.label-checkbox100::before {
	  content: "\f00c";
	  font-family: FontAwesome;
	  font-size: 13px;
	  color: transparent;
	
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  position: absolute;
	  width: 18px;
	  height: 18px;
	  border-radius: 3px;
	  background: #fff;
	  border: 2px solid rgb(211,71,54);
	  left: 0;
	  top: 48%;
	  -webkit-transform: translateY(-50%);
	  -moz-transform: translateY(-50%);
	  -ms-transform: translateY(-50%);
	  -o-transform: translateY(-50%);
	  transform: translateY(-50%);
	}
	
	.input-checkbox100:checked + .label-checkbox100::before {
	  color: rgb(211,71,54);
	}
	
	
	/*------------------------------------------------------------------
	[ Button ]*/
	.container-login100-form-btn {
	  width: 100%;
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  flex-wrap: wrap;
	  justify-content: center;
	}
	
	.login100-form-btn {
	  font-family: Raleway-Bold;
	  font-size: 16px;
	  line-height: 1.5;
	  color: #fff;
	  text-transform: uppercase;
	
	  width: 100%;
	  height: 42px;
	  border-radius: 23px;
	  background:#002363;
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  padding: 0 25px;
	
	  -webkit-transition: all 0.4s;
	  -o-transition: all 0.4s;
	  -moz-transition: all 0.4s;
	  transition: all 0.4s;
	}
	
	.login100-form-btn:hover {
	  background: #333333;
	}
	
	/*------------------------------------------------------------------
	[ Button sign in with ]*/
	.btn-face,
	.btn-google {
	  font-family: Raleway-Bold;
	  font-size: 16px;
	  line-height: 1.2;
		cursor: pointer;
	  display: -webkit-box;
	  display: -webkit-flex;
	  display: -moz-box;
	  display: -ms-flexbox;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  width: calc((100% - 10px) / 2);
	  height: 40px;
	  border-radius: 3px;
	  border: 1px solid #e6e6e6;
	  background-color: #fff;
	
	  -webkit-transition: all 0.4s;
	  -o-transition: all 0.4s;
	  -moz-transition: all 0.4s;
	  transition: all 0.4s;
	}
	
	
	.btn-face {
	  color: white;
	  background-color:rgb(70,90,150);
	  border-radius: 40px;
	  width:100%;
	}
	
	.btn-face i {
	  font-size: 20px;
	  margin-right: 10px;
	  padding-bottom: 1px;
	  
	}
	
	.btn-google {
		color: white;
		background-color:rgb(211,71,54);
		border-radius: 40px;
		width:100%;
	}
	
	.btn-google img {
	  width: 19px;
	  margin-right: 10px;
	  padding-bottom: 1px;
	}
	
	
	.btn-face:hover,
	.btn-google:hover {
	  
	  color:white
	}
	
	
	
	/*------------------------------------------------------------------
	[ Alert validate ]*/
	
	.validate-input {
	  position: relative;
	}
	
	.alert-validate::before {
	  content: attr(data-validate);
	  position: absolute;
	  max-width: 70%;
	  background-color: white;
	  border: 1px solid #c80000;
	  border-radius: 3px;
	  padding: 4px 25px 4px 10px;
	  top: 50%;
	  -webkit-transform: translateY(-50%);
	  -moz-transform: translateY(-50%);
	  -ms-transform: translateY(-50%);
	  -o-transform: translateY(-50%);
	  transform: translateY(-50%);
	  right: 8px;
	  pointer-events: none;
	
	  font-family: Raleway-SemiBold;
	  color: #c80000;
	  font-size: 13px;
	  line-height: 1.4;
	  text-align: left;
	
	  visibility: hidden;
	  opacity: 0;
	
	  -webkit-transition: opacity 0.4s;
	  -o-transition: opacity 0.4s;
	  -moz-transition: opacity 0.4s;
	  transition: opacity 0.4s;
	}
	
	.alert-validate::after {
	  content: "\f12a";
	  font-family: FontAwesome;
	  display: block;
	  position: absolute;
	  color: #c80000;
	  font-size: 15px;
	  top: 50%;
	  -webkit-transform: translateY(-50%);
	  -moz-transform: translateY(-50%);
	  -ms-transform: translateY(-50%);
	  -o-transform: translateY(-50%);
	  transform: translateY(-50%);
	  right: 13px;
	}
	
	.alert-validate:hover:before {
	  visibility: visible;
	  opacity: 1;
	}
	.input-group-text i{
		font-size: 1.6em;
	}
}
  
  
  @media (max-width: 992px) {
	.login_limiter{
		.alert-validate::before {
			visibility: visible;
			opacity: 1;
		  }
	}
	
  }
  
  /*//////////////////////////////////////////////////////////////////
  [ responsive ]*/
  
  @media (max-width: 480px) {
	.login_limiter{
		.wrap-login100 {
			padding-left: 15px !important;
			padding-right: 15px !important;
				
		}
		.forget_password_label{
			margin-left: 10px !important;
		}
		
	}
  }